<template>
  <div>
    <h3>Simple Table</h3>
    <v-simple-table>
      <template v-slot:default>
        <thead v-columns-resizable>
          <draggable v-model="headers" tag="tr">
            <th v-for="header in headers" :key="header.value" scope="col">
              {{ header.text }}
            </th>
          </draggable>
        </thead>

        <draggable v-model="desserts" tag="tbody">
          <tr v-for="item in desserts" :key="item.name">
            <td v-for="header in headers" :key="header.value">
              {{ item[header.value] }}
            </td>
          </tr>
        </draggable>
      </template>
    </v-simple-table>

    <h3>Data Table</h3>
    <v-data-table :headers="headers" :items="desserts" :sort-by="['name']"
    :sort-desc="[false, true]">
      <!-- <template v-slot:header.name="{ header }">
        {{ header.text.toUpperCase() }}
      </template> -->
      <template slot="header" slot-scope="props">
        <thead v-columns-resizable>
          <draggable v-model="headers" tag="tr">
            <th
              v-for="header in props.props.headers"
              :key="header.value"
              scope="col"
            >
              {{ header.text }}
            </th>
          </draggable>
        </thead>
      </template>

      <template slot="item" slot-scope="props">
          <draggable v-model="desserts" tag="tr">
            <td v-for="header in props.headers" :key="header.value">
              {{ props.item[header.value] }}
            </td>
        </draggable>
      </template>
    </v-data-table>

    <h3>Data Table + directive</h3>
    <v-data-table 
        :headers="headers" 
        :items="desserts" 
        :sort-by="['name']"
        :sort-desc="[false, true]" :key="anIncreasingNumber" 
        v-sortable-table="{onEnd:sortTheHeadersAndUpdateTheKey}">
    </v-data-table>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import Sortable from "sortablejs";

// Add back the sortHandle class if it gets stripped away by external code
function watchClass(targetNode, classToWatch) {
  let lastClassState = targetNode.classList.contains(classToWatch);
  const observer = new MutationObserver((mutationsList) => {
    for (let i = 0; i < mutationsList.length; i++) {
      const mutation = mutationsList[i];
      if (
        mutation.type === "attributes" &&
        mutation.attributeName === "class"
      ) {
        const currentClassState = mutation.target.classList.contains(
          classToWatch
        );
        if (lastClassState !== currentClassState) {
          lastClassState = currentClassState;
          if (!currentClassState) {
            mutation.target.classList.add("sortHandle");
          }
        }
      }
    }
  });
  observer.observe(targetNode, { attributes: true });
}

export default {
  components: {
    draggable,
  },
  data() {
    return {
     anIncreasingNumber: 1,
      headers: [
        {
          text: "Dessert (100g serving)",
          value: "name",
          width: "200px",
          fixed: true,
          sortable: true,
        },
        { text: "Calories", value: "calories", width: "200px", fixed: true },
        { text: "Fat (g)", value: "fat", width: "300px" },
        { text: "Carbs (g)", value: "carbs", width: "300px" },
        { text: "Protein (g)", value: "protein", width: "300px" },
        { text: "New Protein (g)", value: "nprotein", width: "300px" },
        { text: "Iron (%)", value: "iron", width: "300px" },
        { text: "New Iron (%)", value: "niron", width: "300px" },
      ],
      desserts: [
        {
          value: false,
          name: "Orange Juice",
          category: "Beverage",
          calories: 262,
          fat: 16.0,
          carbs: 23,
          protein: 6.0,
          nprotein: 6.0,
          iron: "7%",
          niron: "7%",
        },
        {
          value: false,
          name: "Larabar",
          category: "Snack",
          calories: 408,
          fat: 3.2,
          carbs: 87,
          protein: 6.5,
          nprotein: 6.5,
          iron: "45%",
          niron: "45%",
        },
        {
          value: false,
          name: "Donut",
          category: "Breakfast",
          calories: 452,
          fat: 25.0,
          carbs: 51,
          protein: 4.9,
          nprotein: 4.9,
          iron: "22%",
          niron: "22%",
        },

        {
          value: false,
          name: "Bagel",
          category: "Breakfast",
          calories: 999,
          fat: 28.0,
          carbs: 151,
          protein: 2.9,
          nprotein: 2.9,
          iron: "29%",
          niron: "29%",
        },
        {
          value: false,
          name: "KitKat",
          category: "Snack",
          calories: 518,
          fat: 26.0,
          carbs: 65,
          protein: 7,
          nprotein: 7,
          iron: "6%",
          niron: "6%",
        },
      ],
    };
  },
  methods: {
    sortTheHeadersAndUpdateTheKey(evt) {
      const headersTmp = this.headers;
      const oldIndex = evt.oldIndex;
      const newIndex = evt.newIndex;
      if (newIndex >= headersTmp.length) {
        let k = newIndex - headersTmp.length + 1;
        while (k--) {
          headersTmp.push(undefined);
        }
      }
      headersTmp.splice(newIndex, 0, headersTmp.splice(oldIndex, 1)[0]);
      this.table = headersTmp;
      this.anIncreasingNumber += 1;
    },
  },
  directives: {
    "sortable-table": {
      inserted: (el, binding) => {
        el.querySelectorAll("th").forEach((draggableEl) => {
          // Need a class watcher because sorting v-data-table rows asc/desc removes the sortHandle class
          watchClass(draggableEl, "sortHandle");
          draggableEl.classList.add("sortHandle");
        });
        Sortable.create(
          el.querySelector("tr"),
          binding.value ? { ...binding.value, handle: ".sortHandle" } : {}
        );
      },
    },
  },
};
</script>

<style scoped>
th {
  color: #fff !important;
  background-color: #343a40 !important;
  border-left: 1px solid #fff !important;
  vertical-align: bottom !important;
  border-bottom: 2px solid #dee2e6 !important;
  padding: 0.75rem 0 !important;
}
</style>
